export default {
	data() {
		return {
			amount:0,
			orderName:'',
			paytype:'alipay'//支付类型
		};
	},
	onLoad(e) {
		this.amount = parseFloat(e.amount).toFixed(2);
		uni.getStorage({
			key:'paymentOrder',
			success: (e) => {
				if(e.data.length>1){
					this.orderName = '多商品合并支付'
				}else{
					this.orderName = e.data[0].name;
				}
				uni.removeStorage({
					key:'paymentOrder'
				})
			}
		})
	},
	methods:{
		doDeposit(){
			//模板模拟支付，实际应用请调起微信/支付宝
			uni.showLoading({
				title:'支付中...'
			});
			var buylist;
			uni.getStorage({
				key:"buydata",
				success: res => {
					buylist=res.data;
					var buylists=[];
					for(var item in buylist)
					{
							buylist[item]["type"]="back";
							buylist[item]["goods_id"]=buylist[item].id;
							buylist[item]["payment"]=buylist[item].price;
							buylist[item]["ordersn"]=item;
					};
					console.log(buylist,"8888888888888888888888888888")
					uni.setStorage({
						key:"lanzelist",
						data:buylist,
						success: res => {
							console.log("成功");
						},
						fail: res => {
							console.log("失败")
						}
					})
					var gouwuche;
					uni.getStorage({
						key:"shoucanlist",
						success:res => {
							gouwuche=res.data;
							var myid=[];
							if(buylists.length!==0)
							{
								for(var item of buylists)
								{
									myid.push(item.goods_id);
								};
								var gouwuchelist=[];
								for(var i=0;i<myid.length;i++)
								{
									for(var j=0;j<gouwuche.length;j++)
									{
										if(gouwuche[j]["goods_id"]==myid[i])
										{
											gouwuchelist.push(gouwuche[j])
										}
									}
								};
								uni.setStorage({
									key:"shoucanlist",
									data:gouwuchelist,
									success: res => {
										console.log("成功");
									}
								})
							}
							
						}
					})
				}
			})
			setTimeout(()=>{
				uni.hideLoading();
				uni.showToast({
					title:'支付成功'
				});
				setTimeout(()=>{
					uni.redirectTo({
						url:'../../pay/success/success?amount='+this.amount
					});
				},300);
			},700)
		}
	}
}
